"use client"

import { useState, useEffect, useCallback } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import Image from "next/image"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"
import { 
  Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter 
} from "@/components/ui/dialog"
import { 
  Plus, Play, Square, Slack, LogOut, Activity, 
  Sparkles, UserCheck, Eye, ShieldCheck, Zap, Info, Send, ChevronLeft, ChevronRight, Loader2
} from "lucide-react"

// --- 인터페이스 수정 (백엔드 DTO 기준) ---
interface Promotion {
  id: string; // UUID
  title: string;
  description: string;
  rewardPoints: number;
  maxParticipants: number;
  currentParticipants: number;
  status: "SCHEDULED" | "ACTIVE" | "ENDED";
}

interface AdvisorRequest {
  userId: string;
  userName: string;
  name: string;
  email: string;
  appliedAt: string;
  reason: string;
}

export default function AdminDashboard() {
  const router = useRouter()
  const [isMounted, setIsMounted] = useState(false)
  const [isLoading, setIsLoading] = useState(false)
  const [userRole, setUserRole] = useState<string | null>(null)
  const [userName, setUserName] = useState("")

  // API 베이스 URL
  const PROMO_API = "http://34.50.7.8/v1/promotions"
  const USER_API = "http://34.50.7.8/v1/user"

  // 상태 관리
  const [promotions, setPromotions] = useState<Promotion[]>([])
  const [advisorRequests, setAdvisorRequests] = useState<AdvisorRequest[]>([])
  const [selectedRequest, setSelectedRequest] = useState<AdvisorRequest | null>(null)
  const [isDetailOpen, setIsDetailOpen] = useState(false)
  const [isPromoCreateOpen, setIsPromoCreateOpen] = useState(false)

  // 페이징 상태
  const [promoPage, setPromoPage] = useState(0) // 백엔드는 0부터 시작
  const [totalPromoPages, setTotalPromoPages] = useState(1)
  const [advisorPage, setAdvisorPage] = useState(1)
  const itemsPerPage = 5 

  // --- 1. 프로모션 목록 조회 (API) ---
  const fetchPromotions = useCallback(async () => {
    const token = localStorage.getItem("accessToken")
    try {
      setIsLoading(true)
      const res = await fetch(`${PROMO_API}?page=${promoPage}&size=${itemsPerPage}`, {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      if (data.isSuccess) {
        setPromotions(data.result.content) // Page 객체의 content
        setTotalPromoPages(data.result.totalPages)
      }
    } catch (error) {
      console.error("프로모션 로드 실패", error)
    } finally {
      setIsLoading(false)
    }
  }, [promoPage])

  // --- 2. 전문가 심사 대기 목록 조회 (API) ---
  const fetchPendingAdvisors = useCallback(async () => {
    const token = localStorage.getItem("accessToken")
    try {
      const res = await fetch(`${USER_API}/admin/advisor/pending`, {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      if (data.isSuccess) {
        setAdvisorRequests(data.result)
      }
    } catch (error) {
      console.error("심사 목록 로드 실패", error)
    }
  }, [])

  useEffect(() => {
    const role = localStorage.getItem("role")
    const name = localStorage.getItem("name") || "관리자"
    
    if (role !== "MANAGER" && role !== "MASTER") {
      alert("접근 권한이 없습니다.")
      router.push("/login")
      return
    }

    setUserRole(role)
    setUserName(name)
    setIsMounted(true)
    fetchPromotions()
    fetchPendingAdvisors()
  }, [router, fetchPromotions, fetchPendingAdvisors])

  // --- 3. 프로모션 생성 (API) ---
  const [newPromo, setNewPromo] = useState({ title: "", rewardPoints: "", maxParticipants: "", description: "" })

  const handleCreatePromotion = async () => {
    const token = localStorage.getItem("accessToken")
    if (!newPromo.title || !newPromo.rewardPoints) return alert("필수 정보를 입력하세요.")

    try {
      const res = await fetch(PROMO_API, {
        method: "POST",
        headers: { 
          "Authorization": `Bearer ${token}`,
          "Content-Type": "application/json"
        },
        body: JSON.stringify({
          title: newPromo.title,
          description: newPromo.description,
          rewardPoints: Number(newPromo.rewardPoints),
          maxParticipants: Number(newPromo.maxParticipants)
        })
      })
      const data = await res.json()
      if (data.isSuccess) {
        alert("프로모션이 생성되었습니다.")
        setIsPromoCreateOpen(false)
        setNewPromo({ title: "", rewardPoints: "", maxParticipants: "", description: "" })
        fetchPromotions()
      }
    } catch (error) {
      alert("생성 중 오류 발생")
    }
  }

  // --- 4. 프로모션 시작/종료 토글 (API) ---
  const handleTogglePromotion = async (id: string, currentStatus: string) => {
    const token = localStorage.getItem("accessToken")
    const isStarting = currentStatus === "SCHEDULED"
    const action = isStarting ? "start" : "end"
    
    if (!confirm(`프로모션을 ${isStarting ? "시작" : "종료"}하시겠습니까?`)) return

    try {
      const res = await fetch(`${PROMO_API}/${id}/${action}`, {
        method: "PATCH",
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      if (data.isSuccess) {
        fetchPromotions()
      } else {
        alert(data.message)
      }
    } catch (error) {
      alert("상태 변경 실패")
    }
  }

  // --- 5. 전문가 승인 (API) ---
  const handleApproveAdvisor = async (userId: string) => {
    const token = localStorage.getItem("accessToken")
    try {
      const res = await fetch(`${USER_API}/admin/advisor/${userId}/approve`, {
        method: "POST",
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      if (data.isSuccess) {
        alert("승인되었습니다.")
        setIsDetailOpen(false)
        fetchPendingAdvisors()
      }
    } catch (error) {
      alert("승인 처리 실패")
    }
  }

  const handleLogout = () => {
    if (confirm("로그아웃 하시겠습니까?")) {
      localStorage.clear()
      router.push("/login")
    }
  }

  if (!isMounted) return null

  return (
    <div className="min-h-screen bg-background text-foreground font-sans">
      <header className="border-b border-border bg-card/50 backdrop-blur-sm sticky top-0 z-50">
        <div className="container mx-auto px-4 h-16 flex items-center justify-between">
          <Link href="/admin" className="flex items-center gap-2">
            <Image src="/logo.png" alt="Next Me" width={120} height={32} className="h-8 w-auto" priority />
            <Badge variant="outline" className="ml-2 border-primary text-primary font-bold uppercase">
              {userRole} ADMIN
            </Badge>
          </Link>
          <div className="flex items-center gap-4">
            <div className="hidden md:block text-right">
              <p className="text-sm font-bold leading-none">{userName}님</p>
            </div>
            <Avatar className="h-9 w-9 border-2 border-primary/20 shadow-sm">
              <AvatarFallback className="bg-primary text-primary-foreground text-xs font-black uppercase">
                {userName?.charAt(0)}
              </AvatarFallback>
            </Avatar>
            <Button variant="ghost" size="icon" onClick={handleLogout} className="rounded-xl hover:bg-destructive/10 hover:text-destructive transition-all">
              <LogOut className="h-5 w-5" />
            </Button>
          </div>
        </div>
      </header>

      <main className="container mx-auto px-4 py-8">
        {/* 상단 타이틀 및 슬랙 버튼 */}
        <div className="flex justify-between items-end mb-8">
          <div>
            <h1 className="text-3xl font-bold mb-2 tracking-tighter uppercase font-black italic">환영합니다, <span className="text-foreground font-bold">{userName}</span> 관리자님.</h1>
          </div>
          <div className="flex gap-2">
            <Button variant="outline" size="sm" className="rounded-xl" onClick={() => alert("Slack 연동 테스트")}>
              <Slack className="h-4 w-4 mr-2 text-[#4A154B]" /> Slack 테스트
            </Button>
            <Button size="sm" className="bg-primary rounded-xl" onClick={() => alert("슬랙 리포트 전송 완료")}>
              <Send className="h-4 w-4 mr-2" /> 슬랙 보고서 전송
            </Button>
          </div>
        </div>

        {/* 대시보드 요약 카드 */}
        <div className="grid md:grid-cols-3 gap-6 mb-8">
          <Card className="p-6 border-border shadow-none rounded-2xl">
            <div className="flex items-center justify-between mb-4">
              <div className="h-10 w-10 rounded-xl bg-primary/10 flex items-center justify-center"><Activity className="h-5 w-5 text-primary" /></div>
              <Badge className="bg-green-500 text-white border-none rounded-full px-2 text-[10px]">ACTIVE</Badge>
            </div>
            <p className="text-xs text-muted-foreground mb-1 font-black italic uppercase tracking-tighter">System Status</p>
            <p className="text-2xl font-black font-sans tracking-tight">Operational</p>
          </Card>
          <Card className="p-6 border-border shadow-none rounded-2xl">
            <div className="flex items-center justify-between mb-4">
              <div className="h-10 w-10 rounded-xl bg-accent/10 flex items-center justify-center"><Zap className="h-5 w-5 text-accent" /></div>
            </div>
            <p className="text-xs text-muted-foreground mb-1 font-black italic uppercase tracking-tighter">Running Promos</p>
            <p className="text-2xl font-black font-sans tracking-tight">{promotions.filter(p=>p.status==="ACTIVE").length} 건</p>
          </Card>
          <Card className="p-6 border-border shadow-none rounded-2xl">
            <div className="flex items-center justify-between mb-4">
              <div className="h-10 w-10 rounded-xl bg-orange-500/10 flex items-center justify-center"><UserCheck className="h-5 w-5 text-orange-500" /></div>
              <Badge variant="destructive" className="animate-pulse rounded-full px-2 text-[10px]">{advisorRequests.length}</Badge>
            </div>
            <p className="text-xs text-muted-foreground mb-1 font-black italic uppercase tracking-tighter">Pending Tasks</p>
            <p className="text-2xl font-black font-sans tracking-tight">심사 대기중</p>
          </Card>
        </div>

        <div className="grid lg:grid-cols-3 gap-6 items-start">
          <div className="lg:col-span-2">
            <Tabs defaultValue="promotions" className="w-full">
              <TabsList className="bg-secondary/30 mb-6 p-1 rounded-xl">
                <TabsTrigger value="promotions" className="font-bold rounded-lg px-6">프로모션 센터</TabsTrigger>
                <TabsTrigger value="advisors" className="font-bold rounded-lg px-6">전문가 심사</TabsTrigger>
              </TabsList>

              <TabsContent value="promotions" className="mt-0">
                <Card className="p-6 border-border shadow-none flex flex-col min-h-[500px] rounded-2xl relative">
                  <div className="flex items-center justify-between mb-6">
                    <h2 className="text-xl font-bold flex items-center gap-2 tracking-tight"><Sparkles className="h-5 w-5 text-primary" /> 이벤트 현황</h2>
                    <Button size="sm" onClick={() => setIsPromoCreateOpen(true)} className="rounded-xl px-4 font-bold shadow-lg shadow-primary/20"><Plus className="h-4 w-4 mr-1" /> 신규 등록</Button>
                  </div>
                  
                  <div className="flex-1 overflow-x-auto">
                    {isLoading ? (
                      <div className="h-full flex items-center justify-center py-20"><Loader2 className="animate-spin text-primary" /></div>
                    ) : (
                      <table className="w-full text-left">
                        <thead className="text-muted-foreground text-[10px] font-black uppercase border-b border-border/50 tracking-widest">
                          <tr>
                            <th className="px-2 py-3">Promotion Info</th>
                            <th className="px-2 py-3 text-center">Status</th>
                            <th className="px-2 py-3 text-right">Action</th>
                          </tr>
                        </thead>
                        <tbody className="divide-y divide-border/50 text-sm font-medium">
                          {promotions.map((p) => (
                            <tr key={p.id} className="group hover:bg-secondary/10 transition-all">
                              <td className="px-2 py-4">
                                <p className="font-bold text-foreground line-clamp-1">{p.title}</p>
                                <p className="text-xs text-primary font-mono">{p.rewardPoints.toLocaleString()}P · {p.maxParticipants}명</p>
                              </td>
                              <td className="px-2 py-4 text-center">
                                <Badge variant={p.status === "ACTIVE" ? "default" : "secondary"} className="rounded-md font-black uppercase text-[9px] px-2">
                                  {p.status}
                                </Badge>
                              </td>
                              <td className="px-2 py-4 text-right flex justify-end gap-1">
                                {p.status === "SCHEDULED" && (
                                  <Button size="icon" variant="ghost" className="h-8 w-8 text-green-600 hover:bg-green-50 rounded-lg" onClick={() => handleTogglePromotion(p.id, "SCHEDULED")}><Play className="h-4 w-4 fill-current" /></Button>
                                )}
                                {p.status === "ACTIVE" && (
                                  <Button size="icon" variant="ghost" className="h-8 w-8 text-destructive hover:bg-destructive/5 rounded-lg" onClick={() => handleTogglePromotion(p.id, "ACTIVE")}><Square className="h-4 w-4 fill-current" /></Button>
                                )}
                                <Button size="icon" variant="ghost" className="h-8 w-8 text-muted-foreground rounded-lg"><Info className="h-4 w-4" /></Button>
                              </td>
                            </tr>
                          ))}
                        </tbody>
                      </table>
                    )}
                  </div>

                  <div className="flex items-center justify-center gap-2 mt-6 pt-4 border-t border-border/50">
                    <Button variant="outline" size="icon" className="h-8 w-8 rounded-lg" onClick={() => setPromoPage(p => Math.max(0, p-1))} disabled={promoPage === 0}><ChevronLeft className="h-4 w-4" /></Button>
                    <span className="text-[10px] font-black px-4 italic">{promoPage + 1} / {totalPromoPages}</span>
                    <Button variant="outline" size="icon" className="h-8 w-8 rounded-lg" onClick={() => setPromoPage(p => Math.min(totalPromoPages - 1, p+1))} disabled={promoPage >= totalPromoPages - 1}><ChevronRight className="h-4 w-4" /></Button>
                  </div>
                </Card>
              </TabsContent>

              <TabsContent value="advisors" className="mt-0">
                <Card className="p-6 border-border shadow-none flex flex-col min-h-[500px] rounded-2xl">
                  <h2 className="text-xl font-bold mb-6 flex items-center gap-2 tracking-tight"><UserCheck className="h-5 w-5 text-primary" /> 전문가 자격 심사</h2>
                  <div className="flex-1 grid gap-3">
                    {advisorRequests.length > 0 ? advisorRequests.map((req) => (
                      <div key={req.userId} className="flex items-center justify-between p-4 rounded-2xl bg-secondary/10 border border-transparent group hover:border-primary/20 hover:bg-white transition-all">
                        <div className="flex items-center gap-4">
                          <Avatar className="h-10 w-10 border-2 border-background shadow-sm"><AvatarFallback className="font-bold bg-secondary text-secondary-foreground">{req.name[0]}</AvatarFallback></Avatar>
                          <div><p className="font-bold text-sm tracking-tight">{req.name}</p><p className="text-[10px] text-muted-foreground font-mono uppercase">{req.appliedAt.split('T')[0]}</p></div>
                        </div>
                        <Button variant="outline" size="sm" className="h-8 font-bold rounded-xl px-4 border-primary/20 hover:bg-primary hover:text-white" onClick={() => { setSelectedRequest(req); setIsDetailOpen(true); }}><Eye className="h-3.5 w-3.5 mr-2" /> 심사</Button>
                      </div>
                    )) : (
                      <div className="flex flex-col items-center justify-center py-20 text-muted-foreground italic text-xs uppercase tracking-widest opacity-50">심사 대기 내역이 없습니다.</div>
                    )}
                  </div>
                </Card>
              </TabsContent>
            </Tabs>
          </div>

          {/* 우측 안내 섹션 */}
          <div className="mt-0 lg:mt-[64px]"> 
            <Card className="p-6 border-border shadow-none min-h-[500px] bg-secondary/5 rounded-2xl">
              <h2 className="text-xl font-bold mb-6 flex items-center gap-2 tracking-tight"><ShieldCheck className="h-5 w-5 text-primary" /> 운영 안내</h2>
              <div className="space-y-6 text-sm text-muted-foreground font-medium">
                <div className="space-y-3">
                  <p className="font-bold text-foreground flex items-center gap-2"><span className="w-1.5 h-4 bg-primary rounded-full"></span>데이터 관리</p>
                  <p className="pl-3 border-l-2 border-muted/30 leading-relaxed text-[13px]">프로모션 상태가 <strong>ACTIVE</strong>가 되면 유저 화면에 즉시 노출되며 참여가 시작됩니다.</p>
                </div>
                {userRole === "MASTER" && (
                  <div className="mt-8 p-5 rounded-2xl bg-primary text-primary-foreground shadow-xl shadow-primary/20 relative overflow-hidden">
                    <p className="text-[10px] font-black uppercase italic mb-2 tracking-widest opacity-80">Master Admin Access</p>
                    <p className="text-[12px] leading-tight font-bold">최고 관리자 권한으로 접속 중입니다.</p>
                  </div>
                )}
              </div>
            </Card>
          </div>
        </div>
      </main>

      {/* 모달: 프로모션 생성 */}
      <Dialog open={isPromoCreateOpen} onOpenChange={setIsPromoCreateOpen}>
        <DialogContent className="sm:max-w-[425px] rounded-3xl">
          <DialogHeader><DialogTitle className="font-black italic tracking-tight uppercase text-xl">New Campaign</DialogTitle></DialogHeader>
          <div className="grid gap-4 py-4">
            <div className="space-y-2">
              <Label className="text-[11px] font-black uppercase tracking-wider text-muted-foreground">Title</Label>
              <Input value={newPromo.title} onChange={(e) => setNewPromo({...newPromo, title: e.target.value})} className="bg-secondary/20 border-none h-12 rounded-xl" placeholder="프로모션 제목" />
            </div>
            <div className="grid grid-cols-2 gap-4">
              <div className="space-y-2">
                <Label className="text-[11px] font-black uppercase tracking-wider text-muted-foreground">Points</Label>
                <Input type="number" value={newPromo.rewardPoints} onChange={(e) => setNewPromo({...newPromo, rewardPoints: e.target.value})} className="bg-secondary/20 border-none h-12 rounded-xl" placeholder="5000" />
              </div>
              <div className="space-y-2">
                <Label className="text-[11px] font-black uppercase tracking-wider text-muted-foreground">Quota</Label>
                <Input type="number" value={newPromo.maxParticipants} onChange={(e) => setNewPromo({...newPromo, maxParticipants: e.target.value})} className="bg-secondary/20 border-none h-12 rounded-xl" placeholder="100" />
              </div>
            </div>
            <div className="space-y-2">
              <Label className="text-[11px] font-black uppercase tracking-wider text-muted-foreground">Description</Label>
              <Textarea value={newPromo.description} onChange={(e) => setNewPromo({...newPromo, description: e.target.value})} className="bg-secondary/20 border-none rounded-xl" placeholder="이벤트 상세 내용" />
            </div>
          </div>
          <DialogFooter><Button onClick={handleCreatePromotion} className="w-full h-12 font-black rounded-xl uppercase tracking-tighter">Create Event</Button></DialogFooter>
        </DialogContent>
      </Dialog>

      {/* 모달: 전문가 상세 심사 */}
      <Dialog open={isDetailOpen} onOpenChange={setIsDetailOpen}>
        <DialogContent className="sm:max-w-[480px] rounded-3xl">
          <DialogHeader><DialogTitle className="font-black tracking-tighter text-2xl uppercase">Qualification</DialogTitle></DialogHeader>
          {selectedRequest && (
            <div className="space-y-5 py-4">
              <div className="flex items-center gap-4 p-5 rounded-2xl bg-secondary/20 border border-border/50 shadow-inner">
                <Avatar className="h-16 w-16 border-4 border-white shadow-md"><AvatarFallback className="font-black text-xl bg-primary text-white">{selectedRequest.name[0]}</AvatarFallback></Avatar>
                <div><p className="font-black text-xl leading-tight tracking-tight">{selectedRequest.name}</p><p className="text-xs text-muted-foreground font-mono uppercase tracking-tighter mt-1">{selectedRequest.email}</p></div>
              </div>
              <div className="p-6 bg-card border border-border/50 rounded-2xl text-[14px] leading-relaxed relative overflow-hidden shadow-sm">
                <div className="absolute top-0 left-0 w-1.5 h-full bg-primary/40"></div>
                <p className="font-bold italic">"{selectedRequest.reason}"</p>
              </div>
            </div>
          )}
          <DialogFooter className="gap-3">
            <Button variant="outline" className="flex-1 text-destructive font-black h-12 rounded-xl border-destructive/20 hover:bg-destructive/5" onClick={() => setIsDetailOpen(false)}>닫기</Button>
            <Button className="flex-1 font-black h-12 rounded-xl shadow-lg shadow-primary/30" onClick={() => handleApproveAdvisor(selectedRequest!.userId)}>심사 승인</Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </div>
  )
}